.how-it-works-page .access-passwords-row1{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding:0 10%;
    margin:7.5% 0 5%;
    .col1{
        width: 45%;
        h1{
            font-size: 35px;
            margin-bottom: 15px;
        }
    }
}
.how-it-works-page .access-passwords-row1 .col2{
    position: relative;
    width: 40%;
    .bg-img{
        content: url('../../../../assets/HowItWorks/bg-img.png');
        width: 100%;
    }
    .top-img{
        content: url('../../../../assets/HowItWorks/top-img.png');
        width: 75%;
        border-radius: 50%;
        position: absolute;
        right:10%;
        top:5%;
    }
    .logo{
        content: url('../../../../assets/HowItWorks/blue-logo.png');
        width: 25%;
        position: absolute;
        right: 7.5%;
        top:30%;
    }
    
    &:hover{
        transition: 0.35s ease-in-out;
        .top-img{
            transform: scale(1.035);
            transition: 0.35s ease-in-out;
        }
        .logo{
            filter: brightness(0.85);
        }
    }
}

@media(max-width:1401px){
    .how-it-works-page .access-passwords-row1{
        padding:0 10%;
        .col1 h1{
            font-size: 25px;
            margin-bottom: 10px;
        }
        
    }
}
@media(max-width:1025px){
    .how-it-works-page .access-passwords-row1{
        padding:0 7.5%;
        margin:7.5% 0;
        .col1{
            width: 47.5%;
        }
    }
    .how-it-works-page .access-passwords-row1 .col2{
        width: 42.5%;
    }

}

@media(max-width:767px){
    .how-it-works-page .access-passwords-row1{
        display: block;
        margin:55px 0 40px;
        .col1{
            width:100%;
            margin:25px auto;
            h1{
                font-size: 20px;
            }
        }
    }
    .how-it-works-page .access-passwords-row1 .col2{
        width: 375px;
        max-width: 100%;
        margin:25px auto;
    }
}

//=======================================
.how-it-works-page .access-passwords-row2{
    margin:5% 0;
    padding:0 10%;
    h1{
        font-size: 35px;
    }
    .p{
        font-size: 18px;
        margin:20px 0 50px;
    }
    .items1{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .svg-card{
        width: 28%;
        padding:20px 25px;
    }
}

@media(max-width:1401px){
    .how-it-works-page .access-passwords-row2{
        margin:5% 0;
        h1{
            font-size: 30px;
        }
        .p{
            font-size: 15px;
            margin:15px 0 40px;
        }
        .svg-card{
            width: 29%;
            padding:15px 20px;
        }
    }
}
@media(max-width:991px){
    .how-it-works-page .access-passwords-row2{
        padding:0 7.5%;
        .svg-card{
            width: 30%;
        }
    }
}

@media(max-width:767px){
    .how-it-works-page .access-passwords-row2{
        margin:35px 0;
        h1{
            font-size: 20px;
            text-align: center;
        }
        .p{
            margin:15px 0 20px;
            text-align: center;

        }
        .items1{
            justify-content: center;
        }
        .svg-card{
            width: 46%;
            margin:10px 2%;
        }
    }
}
@media(max-width:450px){
    .how-it-works-page .access-passwords-row2 .svg-card{
        width: 100%;
        margin:5px 0;
    }
}