.how-it-works-page .master-password .left-right{
    margin:7.5% 0;
    padding:0 7.5%;
    .col1{
        width: 37.5%;
    }
    .img{
        width: 50%;
    }
}
.how-it-works-page .shared-get-started{
    margin:0;
}
.how-it-works-page .master-password .last-row{
    margin:2.5% 0;
    padding:3% 10%;
    
    h1{
        font-size: 35px;
        text-align: center;
        margin-bottom: 55px;
    }
    .last-items{
        display: flex;
        justify-content: space-between;
    }
    .item{
        width:30%;
        text-align: center;
    }
    .icon{
        height: 70px;
        margin:0 auto 25px;
    }
    p{
        font-size: 18px;
    }
}

@media(max-width:1401px){
    .how-it-works-page .master-password .last-row{
        padding:5% 10%;
        h1{
            font-size: 30px;
            margin-bottom: 45px;
        }
        .icon{
            height: 60px;
            margin:0 auto 20px;
        }
        p{
            font-size: 15px;
        }
    }
}
@media(max-width:767px){
    .how-it-works-page .master-password .left-right{
        margin:25px 0;
        .col1{
            margin:20px auto;
            width: 100%;
            text-align: center;
            a{
                margin:0 auto;
            }
        }
        .img{
            width:425px;
            max-width: 100%;
            margin:20px auto;
        }
    }
    .how-it-works-page .master-password .last-row{
        margin:30px 0 0;
        padding:35px 7.5%;
        h1{
            font-size: 20px;
            margin-bottom: 25px;
        }
        .last-items{
            flex-wrap: wrap;
        }
        .item{
            width:170px;
            max-width: 100%;
            margin:17.5px auto;
        }
        .icon{
            height: 50px;
            margin:0 auto 15px;
        }
    }
}

@keyframes shine {
    0% {
        transform: translateX(-30px) rotate(-25deg);
    }
        
    100% {
        transform: translateX(250px) rotate(-25deg);
    }
}

.how-it-works-page .master-password .last-row{
    .item{
        position: relative;
        overflow: hidden;
        transition: all 200ms linear;
    }
    .item:hover {
         transform: translateY(-10%);
      }
    .item:hover::after {
        content: '';
        display: block;
        width: 75px;
        height: 175%;
        background: rgb(255,255,255);
        background: linear-gradient(90deg, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 25%, rgba(255,255,255,1) 50%, rgba(255,255,255,1) 75%, rgba(255,255,255,0) 100%);
        opacity: 0.8;
        position: absolute;
        top: -20px;
        left: 0;
        animation: shine 500ms linear;
        transform: translateX(1000px) rotate(-25deg);
    }
}