.how-it-works-page{
    padding-top: 70px;
    overflow: hidden;
    .nav{
        background-color: #fff;
        border-bottom: 1px solid #ccc;

        .nav-logo{
            .logo-container{
                content: url('../../../../assets/Navbar/blue-logo.png') !important;
            }
            h6{
                color:#111 !important;
            }
        }
        .nav-links{
            .inner-nav-link{
                color:#000 !important;
                svg{
                    color:#444;
                }
            }
        }
        .nav-auth{
            .nav-login a{
                color:#000 !important;
            }
        }
        .how-it-works-link .inner-nav-link{
            color: #427CFF !important;
        }
    }
    .nav__sticky{
        border:0;
    }
    .mobile-toolbar{
        background-color: white;
        border-bottom: 1px solid #ccc;
        transition: 0.2s ease;
        .mobile-logo-row{
            h6{
                color:#333 !important;
            }
        }
        button{
            color:#222 !important;
        }
    }
    .toolbar__sticky{
        border: 0;
    }
}

//=============================================
.how-it-works-page .banner{
    background-image: url('../../../../assets/HowItWorks/banner.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    padding:15% 7.5% 12.5% 7.5%;
    color:#fff;
    clip-path: polygon(0 0, 100% 0%, 100% 85%, 0% 100%);
    position: relative;
    .img{
        content: url('../../../../assets/HowItWorks/img1.png');
        width: 42%;
        position: absolute;
        right: 0;
        bottom:0;
    }
    .left{
        width: 40%;
    }
    h1{
        font-size: 50px;
        margin-bottom: 20px;
    }
    p{
        font-size: 18px;
    }
}

@media(max-width:1401px){
    .how-it-works-page .banner{
        .img{
            width: 42%;
        }
        .left{
            width: 40%;
        }
        h1{
            font-size: 35px;
        }
        p{
            font-size: 15px;
        }
    }
}

@media(max-width:991px){
    .how-it-works-page .banner{
        .img{
            width: 50%;
        }
        h1{
            font-size: 27px;
        }
    }
}

@media(max-width:767px){
    .how-it-works-page .banner{
        padding:110px 7.5% 300px;
        clip-path: polygon(0 0, 100% 0%, 100% 85%, 0% 100%);
        .img{
            width: 400px;
            max-width: 90%;
            left: 0;
            bottom:0;
            margin-left: auto;
            margin-right: auto;
        }
        .left{
            width: 100%;
            text-align: center;
        }
        h1{
            font-size: 22.5px;
            margin-bottom: 15px;
        }
    }
}

@media(max-width:445px){
    .how-it-works-page .banner{
        padding:110px 7.5% 70%;
    }
}

//==============================================
.how-it-works-page .save-passwords .leftright-reverse{
    margin:5% 0;
    padding:0 10%;
    .col1{
        width: 37.5%;
    }
    .other-elements{
        width: 47.5%;
    }
    .created-img{
        width: 100%;
        position: relative;
    }
    .bg-img{
        content: url('../../../../assets/HowItWorks/img2.png');
        width: 100%;
        border:1px solid #eee;
        border-radius: 10px;
    }
    .inner-created-img{
        position: absolute;
        width: 60%;
        background-color: #fff;
        border:1px solid #eee;
        border-radius: 10px;
        bottom: 0;
        right: 10%;
        padding:20px;
    }
    .header{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .header .logo{
        content: url('../../../../assets/Navbar/blue-logo.png');
        width: 37px;
    }
    .header h5{
        font-size: 18px;
        font-weight: 600;
        flex: 1;
        margin-right: 15px;
    }
    .logo2-container{
        display: flex;
        align-items: flex-start;
        margin:20px 0;
        p{
            font-size: 17px;
            margin-bottom: 3px;
        }
        h6{
            font-size: 15px;
            margin-bottom: 20px;
        }
        span{
            font-size: 25px;
            letter-spacing: 2px;
        }
    }
    .logo2{
        content: url('../../../../assets/HowItWorks/logo2.png');
        width: 55px;
        margin-right: 20px;
    }
    .created-img button{
        background: #427CFF;
        width: 100%;
        height: 40px;
        border:0;
        font-size: 17px;
        color:#fff;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 20px;
        cursor: pointer;
    }
}

.how-it-works-page .save-passwords{
    .inner-created-img{
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
    }
    .inner-created-img:before {
        content: "";
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: linear-gradient(15deg,rgba(66, 124, 255, 0.4),rgba(66, 124, 255, 0.9));
        border-radius: 10px;
        -webkit-transform: scaleY(0);
        transform: scaleY(0);
        -webkit-transform-origin: 50% 100%;
        transform-origin: 50% 100%;
        -webkit-transition-property: transform;
        transition-property: transform;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        -webkit-transition-timing-function: ease-out;
        transition-timing-function: ease-out;
    }
      .inner-created-img:hover{
        color: white;
        button{
            background: #fff;
            color:#427CFF;
        }
    }
    .inner-created-img:hover:before{
        -webkit-transform: scaleY(1);
        transform: scaleY(1);
    }
}

