.how-it-works-page .tab-row{
    margin:7.5% 0;
    padding:0 10%;
    .h1{
        font-size: 35px;
        text-align: center;
        margin-bottom: 35px;
    }
    .half-img{
        content: url('../../../../assets/HowItWorks/half-img.png');
        width: 27.5%;
        margin:0 auto;
    }
    .react-tabs__tab-list{
        width: fit-content;
        border:1px solid #ddd;
        display: flex;
        border-radius: 5px;
        margin:0 auto 100px;
    }
    .react-tabs__tab{
        padding:10px 45px;
        margin:3px;
        border:1.5px solid transparent;
        font-size: 18px;
        color:#333;
        background-color: #fff;
        cursor: pointer;
    }
    .react-tabs__tab--selected{
        border:1.5px solid #427CFF;
        color:#427CFF;
        border-radius: 5px;
        background-color: #f0f4ff;
    }
}

.how-it-works-page .react-tabs__tab-panel{
    display: flex;
    align-items: center;
    justify-content: space-between;
    .info{
        width: 35%;
    }
    .img{
        width: 47.5%;
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
        border-radius: 10px;
    }
    .img1{
        content: url('../../../../assets/HowItWorks/tab1.png');
    }
    .img2{
        content: url('../../../../assets/HowItWorks/tab2.png');
    }
    h1{
        font-size: 30px;
    }
    p{
        font-size: 18px;
        margin:25px 0;
    }
    .logos{
        display: flex;
        align-items: center;
        margin-bottom: 30px;
    }
    .logo{
        width: 50px;
        height: 55px;
        margin:0 10px;
        transition: 0.25s ease-in-out;
        &:hover{
            transform: scale(1.1);
        }
    }
    .logo1{
        content: url('../../../../assets/Apps/logo1.png');
    }
    .logo2{
        content: url('../../../../assets/Apps/logo2.png');
    }
    .logo3{
        content: url('../../../../assets/Apps/logo3.png');
    }
    .logo4{
        content: url('../../../../assets/Apps/logo4.png');
    }
    .logo5{
        content: url('../../../../assets/Apps/logo5.png');
    }
}

@media(max-width:1401px){
    .how-it-works-page .tab-row{
        .h1{
            font-size: 30px;
            margin-bottom: 30px;
        }
        .half-img{
            width: 32.5%;;
        }
        .react-tabs__tab-list{
            margin:0 auto 90px;
        }
        .react-tabs__tab{
            padding:10px 40px;
            margin:2px;
            font-size: 15px;
        }
    }
    
    .how-it-works-page .react-tabs__tab-panel{
        .info{
            width: 37.5%;
        }
        .img{
            width: 50%;
            box-shadow: rgba(99, 99, 99, 0.1) 0px 1px 5px 0px;
        }

        h1{
            font-size: 25px;
        }
        p{
            font-size: 15px;
            margin:20px 0;
        }
        .logos{
            margin-bottom: 25px;
        }
        .logo{
            width: 40px;
            height: 40px;
            margin:0 7.5px;
        }
    }
}

@media(max-width:900px){
    .how-it-works-page .tab-row{
        margin:40px 0;
        padding:0 7.5%;
        .h1{
            font-size: 20px;
            margin-bottom: 20px;
        }
        .half-img{
            width: 280px;
            max-width: 100%;
        }
        .react-tabs__tab-list{
            margin:0 auto 35px;
        }
        .react-tabs__tab{
            padding:10px 20px;
            border:1px solid transparent;
            font-size: 14px;
            text-align: center;
        }
        .react-tabs__tab--selected{
            border:1px solid #427CFF;
        }
    }
    
    .how-it-works-page .react-tabs__tab-panel{
        display: block;
        .info{
            width: 450px;
            max-width: 100%;
            margin:30px auto;
            text-align: center;
            a{
                margin:0 auto;
            }
        }
        .img{
            width: 400px;
            max-width: 100%;
            margin:30px auto;

        }
        .logo{
            width: 35px;
            height: 35px;
        }
        .logos{
            justify-content: center;
        }
        h1{
            font-size: 17.5px;
        }
    }
}